
import './App.css';
import Image from './images/background.jpg'; // Import using relative path
import PersonalArea from './pages/PersonalArea/PersonalArea';
import { HashRouter, Switch, Route } from 'react-router-dom';
import HomePage from './pages/HomePage/HomePage';
import WorkArea from './pages/WorkArea/WorkArea';
import DentalNavBar from './components/DentalNavBar';
import { useEffect, useState } from 'react';
import User from './data/User';
import ContactUs from './pages/ContactUs/ContactUs';
import About from './pages/About/About';
import Appointment from './data/Appointment';
import Appointments from './pages/Appointments/Appointments';
import {getDateOnly} from './Utils/Utils.js';
import Parse from 'parse';
import { CircularProgress } from '@material-ui/core';
import SignUpPage from './pages/SignUpPage/SignUpPage';


function App() {
 const [activeUser, setActiveUser] = useState(null);
 const [jobArray, setJobArray] = useState([]);
 const [employeeArray, setEmployeeArray] = useState([]);
 //const [loading, setLoading] = useState(false);

 
 const styles = {
  paperContainer: {
      height: "100vh",
      backgroundImage: `url(${Image})`,
      backgroundSize: 'cover',
      color: "black",
  }
};


  // let appointments = [
  //   new Appointment("1", "2021-01-19 08:34:56","c0m8khpWrn", "pMUKng8lhG", "    ,      8   .  ", "", "2005-02-12 12:34:56"),
  //   new Appointment("2", "2021-01-19 18:42:56","c0m8khpWrn", "pMUKng8lhG", "    ,   ", "", "2005-02-22 12:34:56"),
  //   new Appointment("3", "2021-01-12 12:34:56","c0m8khpWrn", "pMUKng8lhG", "     1 ,            ,      ", "", "2005-02-22 12:34:56"),
  //   new Appointment("4", "2021-01-19 12:36:56","c0m8khpWrn", "pMUKng8lhG", "      ", "", "2005-02-22 12:34:56"),
  //   new Appointment("5", "2021-01-22 13:10:56","4", "pMUKng8lhG", "        ,    ,   .    ", "", "2005-02-22 12:34:56"),
  //   new Appointment("6", "2021-01-18 13:04:56","6", "pMUKng8lhG", "  -6   ,           .    ", "", "2005-02-22 12:34:56"),
  //   new Appointment("7", "2021-01-22 13:08:56","7", "pMUKng8lhG", "  12,        ,            .   ", "", "2005-02-22 12:34:56"),
  //   new Appointment("8", "2021-01-21 13:20:56","7", "", "  20              .        ,              ", "", "2005-02-22 12:34:56"),
  //   new Appointment("9", "2021-01-22 13:40:56","8", "", "", "   - .   ", "2005-02-22 12:34:56"),
  //   new Appointment("10", "2021-01-22 13:50:56","7", "", "  16      .      ,        .    ,         ", "", "2005-02-22 12:34:56"),
  //   new Appointment("11", "2021-01-19 13:50:56","c0m8khpWrn", "2", "", "", "2005-02-22 12:34:56")
  // ]


  useEffect(()=>{
    let isCancelled = false;

    // Prepare job array
    async function getJobs(){
      const job = Parse.Object.extend('job');
      const jobQuery = new Parse.Query(job);
      const JobList = await jobQuery.find();
      
      if (!isCancelled) {
        let jobs = [];
        JobList.forEach(item => {
          const job =  
            {  
                jobId: item.id,
                jobStr: item.get("job"),
                jobHebrew: item.get("jobHebrew")
            }
            jobs = jobs.concat(job);
        });
        setJobArray(jobs); 
      }
      return true;
    }

    // Prepare doctors array
    async function fetchEmployee() {
      const userQuery = new Parse.Query(Parse.User);
      userQuery.exists("jobId");
      const employeeList = await userQuery.find();
      
      if (!isCancelled) { 
        let employees = [];
        employeeList.forEach(item => {
          const employee =  
            {  
                doctorId: item.id,
                fname: item.get("fname"),
                lname: item.get("lname"),
                fullName: item.get("fname") + " " + item.get("lname"),
                jobId: item.get("jobId").id,
            }
            employees = employees.concat(employee);
        });
        setEmployeeArray(employees);
      }
      return true;
    } 

    getJobs();
    fetchEmployee();

    return () => {
      isCancelled = true;
    };

  }, [])

  function handleLogout ()
  {
    Parse.User.logOut();
    localStorage.clear();
    setActiveUser(null);
  } 
  const handleLogin = (logedUser) => setActiveUser(logedUser);

  return (
//    (!loading) ? 
    <>  
    {console.log("jobArr  "+jobArray.length )} 
      <DentalNavBar activeUser={activeUser} onLogout={handleLogout}/>
      <div className="p-app"   style={styles.paperContainer}>
          <HashRouter>
            <Switch>
              <Route exact path="/"><HomePage  activeUser={activeUser} onLogin={handleLogin}/></Route>
              <Route exact path="/personal"><PersonalArea activeUser={activeUser} employees={employeeArray}/></Route>
              <Route exact path="/personal/:id"><PersonalArea activeUser={activeUser} employees={employeeArray}/></Route>
              <Route exact path="/appointments"><Appointments activeUser={activeUser} jobs={jobArray} employees={employeeArray} /></Route>
              <Route exact path="/appointments/:selectedJobHome"><Appointments activeUser={activeUser} jobs={jobArray} employees={employeeArray} /></Route>
              <Route exact path="/work"><WorkArea  activeUser={activeUser} /></Route>
              <Route exact path="/contactus"><ContactUs activeUser={activeUser}/></Route>
              <Route exact path="/about"><About  activeUser={activeUser}/></Route>
              <Route exact path="/signup"><SignUpPage /></Route>
            </Switch>
          </HashRouter>
      </div>
    </>  
    // :
    // <> 
    //   <CircularProgress
    //             size={40}
    //             left={-20}
    //             top={10}
    //             status={'loading'}
    //             style={{marginLeft: '50%'}}
    //             />
    // </> 
  );
}

export default App;
