import { AppBar, Container, HomeIcon, Button, IconButton, Toolbar, List, ListItem, ListItemText } from "@material-ui/core"
import { Home,  AccessAlarm, ThreeDRotation } from "@material-ui/icons"
import { makeStyles } from "@material-ui/core/styles"
import logo from "../images/logo_small.jpg";

function DentalNavBar(props) {
    const {activeUser, onLogout} = props; 

    const useStyles = makeStyles({
        navbarDisplayFlex: { 
            display: `flex`,
            justifyContent: `space-between`
        },
        navDisplayFlex: { 
            display: `flex`,
            justifyContent: `flex-end`
        },
        linkText: {
          textDecoration: `none`,
          textTransform: `uppercase`,
          color: `white`,
        },
        logo: {
            maxWidth: 40,
            marginRight: '10px'
          },
     });
  
    let navLinks = [

        { title: `  `, path: `#/contactus`},
        { title: `  `, path: `#/about`},
        { title: ` /  `, path: `#/appointments`}
    ]

    if (activeUser)
    {
        if(activeUser.job)
        {
            navLinks = navLinks.concat(
                { title: ` `, path: `#/work` } );
        }
        else
        {
            navLinks = navLinks.concat(
                { title: ` `, path: `#/personal`} );
        }
    }

    const classes = useStyles(); 

    return (
         <AppBar >
            <Toolbar>
                <img src={logo} alt="Kitty Katty!" className={classes.logo} />
                <Container  maxWidth="md" className={classes.navbarDisplayFlex}>
                < IconButton edge="start"   color="inherit"   aria-label="home"  href="#/"  dir="rtl">
                    <Home fontSize="small" />
                </IconButton >               
                <List component="nav" aria-labelledby="main navigation" className={classes.navDisplayFlex} edge="end" >
                    {navLinks.map(({ title, path, onClick }) => (
                    <a href={path} key={title} className={classes.linkText} >
                        <ListItem button>
                        <ListItemText primary={title} />
                        </ListItem>
                    </a>
                    ))}
                    {activeUser ? 
                    <a href="#/" className={classes.linkText} onClick={onLogout}>
                        <ListItem button>
                        <ListItemText primary="" />
                        </ListItem> 
                    </a>  
                    : null}
                </List>
                </Container>
            </Toolbar>
         </AppBar>
        )
}
export default DentalNavBar