//import { Paper } from '@material-ui/core';
// import Image from '../../images/background.jpg'; // Import using relative path
import { Redirect } from "react-router-dom";

import React, { Component, useState } from "react";
import SignUpForm from '../../components/SignUpForm/SignUpForm';
// const axios = require("axios");

import Parse, { User } from 'parse';
  

function SignUpPage(props)
{
    const styles = {
        paperContainer: {
            height: "100vh",
            color: "blue",
        }
    };

    const [errors, setErrors] = useState({});
    const [user, setUser] = useState(
                          {
                            username: "",
                            fname: "",
                            lname: "",
                            bdate: null,
                            sex: "male",
                            phone: "",
                            email: "",
                            password: "",
                            pwconfirm: ""
                          }
                      );

      const [btnTxt, setBtnTxt]  = useState(" ");  
      const [type, setType]  = useState("password");  
      const [score, setScore]  = useState(0);   
      const [doRedirect, setDoRedirect] = useState(false);
      const [redirectTo, setRedirectTo] = useState("");                     

      if (doRedirect)
        return <Redirect to={redirectTo}/>                      

        const validateSignUpForm = () => {
          const errors = {};
          let message = "";
          let isFormValid = true;
        
          if (!user || typeof user.username !== "string" || user.username.trim().length === 0) 
          {
            isFormValid = false;
            errors.username = "   ";
          }

          if (!user || typeof user.fname !== "string" || user.fname.trim().length === 0) 
          {
            isFormValid = false;
            errors.fname = "   ";
          }     

          if (!user || typeof user.lname !== "string" || user.lname.trim().length === 0) 
          {
            isFormValid = false;
            errors.lname = "   ";
          } 

          if (!user || typeof user.bdate !== "object" || user.bdate === null) 
          {
            isFormValid = false;
            errors.bdate = "   ";
          } 
          
          if (!user || typeof user.phone !== "string" || user.phone.trim().length === 0) 
          {
            isFormValid = false;
            errors.phone = "   ";
          }
        
          if (!user || typeof user.email !== "string" || user.email.trim().length === 0)
          {
            isFormValid = false;
            errors.email = "   '' ";
          }
        
          if (!user || typeof user.password !== "string"
          //  || user.password.trim().length < 8
           )
          {
            isFormValid = false;
            errors.password = "    8 ";
          }
        
          if (!user || user.pwconfirm !== user.password)
          {
            isFormValid = false;
            errors.pwconfirm = "  ";
          }
        
          if (!isFormValid) {
            message = "   ";
          }
        
          return {
            success: isFormValid,
            message,
            errors
          };
        };


    function handleDateChange(date) 
    {
      const curUser = Object.assign({}, user);
      curUser.bdate = date;  
      setUser(Object.assign({}, curUser));
    }

    function handleChange(event) {
      setErrors({});
      const field = event.target.name;
      const curUser = Object.assign({}, user);
      curUser[field] = event.target.value; 
      setUser(Object.assign({}, curUser));
    }
  
    function pwHandleChange(event) 
    {
      const field = event.target.name;
      const curUser = Object.assign({}, user);
      curUser[field] = event.target.value;
  
      setUser( Object.assign({}, curUser));
      if (event.target.value === "") 
        setScore(null);
       else 
       {
         if (event.target.value > user.password)
            setScore(score + 1);
          else if (event.target.value < user.password)
            setScore(score - 1);
       }        
    }
  
    function submitSignup() {
        const pUser = new Parse.User()
        pUser.set('username', user.username);
        pUser.set('email', user.email);
        pUser.set('tzeut', user.username);
        pUser.set('fname', user.fname);
        pUser.set('lname', user.lname);
        pUser.set('birthdate', user.bdate);
        pUser.set('sex', user.sex);
        pUser.set('phone', user.phone);
        pUser.set('password', user.password);
        
        pUser.signUp().then((signUpUser) => {
          debugger;
          console.log('User signed up', signUpUser);
          // successful sign up for pacient
          setRedirectTo("/personal");            
          setDoRedirect(true);                         
        }).catch(error => {
          console.error('Error while signing up user', error);
          debugger;
          if (error.code === 202 &&
            error.message === "Account already exists for this username.")
          {
            // window.localStorage.setItem("tzeut",  user.username);
            localStorage.tzeut = user.username;
            setRedirectTo("/");   
            setDoRedirect(true);         
          }
          else
          {
            const errors = {};
            errors.message = error.message;
            setErrors(errors);
          }
        });
    }
  
    function validateForm(event) {
      event.preventDefault();
      const payload = validateSignUpForm();
      if (payload.success) 
      {
        setErrors({}); 
        submitSignup();        
      } 
      else {
          const errors = payload.errors;
          errors.message = payload.message;
          setErrors(errors);
      }
    }
  
    function pwMask(event) {
      event.preventDefault();    
      setType(type === "password" ? "input" : "password");
      setBtnTxt(btnTxt === " " ? " " : " ");
    }
  
      return (
        <div  style={styles.paperContainer}>
          <SignUpForm
            onSubmit={validateForm}
            onChange={handleChange}
            onDateChange={handleDateChange}
            onPwChange={pwHandleChange}
            errors={errors}
            user={user}
            score={score}
            btnTxt={btnTxt}
            type={type}
            pwMask={pwMask}
          />
        </div>
      )
  }  

export default SignUpPage;
