import React from "react";
import "./SignUpForm.css"
import TextField from '@material-ui/core/TextField';
import { Button, FormControl, FormControlLabel, FormLabel, Grid, ListItemIcon, Radio, RadioGroup } from "@material-ui/core";
import { KeyboardDatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import PasswordStr from "../PasswordStr";
import { Alert } from "@material-ui/lab";

const SignUpForm = (props) => {
  const { onSubmit, onChange, errors, user, score, btnTxt,
          type, pwMask, onPwChange, onDateChange } = props;

  const styles = {
    helper: {
         color: '#ca0505',
         fontSize: '.8em',
      },
    alert: {
          width: 250, 
          margin: "auto", 
          fontWeight: 'bold', 
          backgroundColor: "transparent", 
          textIndent: '1em', 
          color: '#ca0505',
          fontSize: '1rem',

    }
  }

  return (
    <div className="c-signup loginBox">
      <h1></h1>
      {errors.message ?
          <Alert color="error" severity="error" style={styles.alert }>
            {errors.message}
          </Alert>
      : <p style={{ height: 33 }}></p>
      }

      <form onSubmit={onSubmit} noValidate autoComplete="off">
        <Grid item xs={12}>
          <TextField 
            FormHelperTextProps={{ style: styles.helper }}
            name="username"
            placeholder=" "
            value={user.username}
            onChange={onChange}
            helperText={errors.username === "" ? '' : errors.username}
            style = {{width: 300, height: 50}}
          />
        </Grid>
        <Grid item xs={12} >
          <TextField
            FormHelperTextProps={{ style: styles.helper }}
            name="fname"
            placeholder=" "
            value={user.fname}
            onChange={onChange}
            helperText={errors.fname === "" ? '' : errors.fname}
            style = {{width: 300, height: 50}}
          />
        </Grid>  
        <Grid item xs={12}>
          <TextField
            FormHelperTextProps={{ style: styles.helper }}
            name="lname"
            placeholder=" "
            value={user.lname}
            onChange={onChange}
            helperText={errors.lname === "" ? '' : errors.lname}
            style = {{width: 300, height: 50}}
          />
        </Grid>  
        <Grid item xs={12} >
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                FormHelperTextProps={{ style: styles.helper }}
                name="bdate"
                placeholder=" "
                variant="dialog"
                format="dd/MM/yyyy"
                value={user.bdate}
                onChange={onDateChange}
                helperText={errors.bdate === "" ? '' : errors.bdate}
                style = {{width: 300, height: 50}}
              />
            </MuiPickersUtilsProvider>
        </Grid>

        <Grid item xs={12} >
          <FormControl component="fieldset">
            <FormLabel component="legend" style = {{padding: "15px 0 8px 0", textAlign: "right" }}></FormLabel>
            <RadioGroup  row >
              <FormControlLabel
                label=""
                control={
                  <Button
                    name="sex"
                    component={Radio}
                    type="radio"
                    value="male"
                    color="secondary"
                    checked={user.sex === "male"} 
                    onClick={onChange}
                    style = {{padding: "0 15px 0 5px", color: "blue"}}
                  />
                }
              />
              <FormControlLabel
                label=""
                control={
                  <Button
                    name="sex"
                    component={Radio}
                    type="radio"
                    value="female"
                    checked={user.sex === "female"}
                    onClick={onChange}
                    style = {{padding: "0 15px 0 5px", color: "blue"}}
                  />
                }
              />
              <FormControlLabel
                label=""
                control={
                  <Button
                    name="sex"
                    component={Radio}
                    type="radio"
                    value="other"
                    checked={user.sex === "other"}
                    onClick={onChange}
                    style = {{padding: "0 15px 0 5px", color: "blue"}}
                  />
                }
              />
            </RadioGroup>
          </FormControl>
        </Grid>

        <Grid item xs={12}>
          <TextField
            FormHelperTextProps={{ style: styles.helper }}
            name="phone"
            placeholder=" "
            value={user.phone}
            onChange={onChange}
            helperText={errors.phone === "" ? '' : errors.phone}
            style = {{width: 300, height: 50, marginTop: 20}}

          />
        </Grid>         
        <Grid item xs={12} >
          <TextField
          FormHelperTextProps={{ style: styles.helper }}
            name="email"
            placeholder="  "
            value={user.email}
            onChange={onChange}
            helperText={errors.email === "" ? '' : errors.email}
            style = {{width: 300, height: 50}}
          />
        </Grid>
        <Grid item xs={12} >
        <TextField
          FormHelperTextProps={{ style: styles.helper }}
          type={type}
          name="password"
          placeholder=""
          value={user.password}
          onChange={onPwChange}
          helperText={errors.password === "" ? '' : errors.password}
          style = {{width: 300, height: 50}}
        />

        <div className="pwStrRow">
          {score >= 1 && (
            <div>
              <PasswordStr score={score} /> 
              <Button 
                className="pwShowHideBtn" 
                color="primary"
                placeholder={btnTxt} 
                onClick={pwMask} 
                style={{position: 'relative', left: '50%', transform: 'translateX(-50%)'}} 
                >{btnTxt} 
              </Button>
            </div>
            )} 
        </div>
        </Grid>
        <TextField
          FormHelperTextProps={{ style: styles.helper }}
          type={type}
          name="pwconfirm"
          placeholder=" "
          value={user.pwconfirm}
          onChange={onChange}
          helperText={errors.pwconfirm === "" ? '' : errors.pwconfirm}
          style = {{width: 300, height: 50}}
        />
        <br />
        <Button 
          className="signUpSubmit"
          primary={true}
          type="submit"
          color="primary"
          variant="contained"
          style = {{width: 300, margin: 40}}
        >
        
        </Button>
      </form>
    </div>
  );
};

export default SignUpForm;
