import { Grid } from "@material-ui/core";
import { useState } from "react";
import { Redirect } from "react-router-dom";
import DigitalServiceComponent from "../../components/DigitalServiceComponent/DigitalServiceComponent";
import LoginComponent from "../../components/LoginComponent/LoginComponent";
import RedirectComponent from "../../components/RedirectComponent/RedirectComponent";
import SignUpComponent from "../../components/SignUpComponent/SignUpComponent";
import User from "../../data/User";
import Parse from 'parse';
import "./HomePage.css"


function HomePage (props)
{
    const {onLogin} = props;

    const [doRedirect, setDoRedirect] = useState(false);

    const activeUser = Parse.User.current() ? new User(Parse.User.current(), "") : null;
    const userTZeut = window.localStorage.getItem("tzeut") ?  window.localStorage.getItem("tzeut") : "";

    function redirectOnLogin(user)
    {
        onLogin(user);
        setDoRedirect(true);
    }

    if (doRedirect && activeUser)
    {
        if (activeUser.job)
            return <Redirect to="/work"/>
        else   
            return <Redirect to="/personal"/>
    }


    return(
        <div className="p-home">
            <Grid container spacing={1}  >
                <Grid container item xs={12} sm={6} spacing={1}>
                    <LoginComponent activeUser={activeUser} onLogin={redirectOnLogin} tzeut={userTZeut}/>
                </Grid>
                <Grid container item xs={12} sm={6} spacing={1}>
                    <RedirectComponent activeUser={activeUser} />
                </Grid>
                <Grid container item xs={12} sm={6} spacing={1}>
                    <SignUpComponent />
                </Grid>
                <Grid container item xs={12} sm={6} spacing={1}>
                    <DigitalServiceComponent />
                </Grid>
            </Grid>
         </div>
    )
}
export default HomePage;